import { deepFreeze, objReduce, noop } from '@ch1/utility';
import { createReducer, spreadPayloadIfNew } from '../../store/redux-utility';
import { Seir } from '../../../seir';
import { defaultDaysToProject } from '../../../constants';
import { camelToSnake, capitalizeFirst } from '../../../utility';
const intGt0 = (value, min = 1) => {
    const number = parseInt(value + '', 10);
    if (number > 0) {
        return number;
    }
    return min;
};
const floatGt0 = (value, min = 0.1) => {
    const number = parseFloat(value + '');
    if (number > 0) {
        return number;
    }
    return min;
};
const reducerDesc = deepFreeze({
    daysToProject: {
        defaultValue: defaultDaysToProject,
        filter: intGt0,
    },
    r0: {
        defaultValue: Seir.r0,
        filter: floatGt0,
    },
    incubationPeriod: {
        defaultValue: Seir.incubationPeriod,
        filter: intGt0,
    },
    durationOfInfection: {
        defaultValue: Seir.durationOfInfection,
        filter: intGt0,
    },
    recoveryTimeForMildCases: {
        defaultValue: Seir.recoveryTimeForMildCases,
        filter: intGt0,
    },
    timeFromIncubationToHospital: {
        defaultValue: Seir.timeFromIncubationToDeath,
        filter: intGt0,
    },
    lengthOfSevereHospitalStay: {
        defaultValue: Seir.lengthOfSevereHospitalStay,
        filter: intGt0,
    },
    timeFromIncubationToDeath: {
        defaultValue: Seir.timeFromIncubationToDeath,
        filter: intGt0,
    },
    hospitalizationRate: {
        defaultValue: Seir.hospitalizationRate,
        filter: floatGt0,
    },
    fatalityRate: {
        defaultValue: Seir.fatalityRate,
        filter: floatGt0,
    },
});
export const camelProp = (prop) => 'updated' + capitalizeFirst(prop);
export const defaultSeirState = deepFreeze(objReduce(reducerDesc, (state, value, prop) => {
    state[prop] = value;
    return state;
}, {}));
const numericSelectors = objReduce(defaultSeirState, (dict, _, prop) => {
    dict[prop] = (state) => state[prop];
    return dict;
}, {});
export const select = Object.assign({}, numericSelectors);
const numericEvents = objReduce(defaultSeirState, (dict, _, prop) => {
    dict[camelProp(prop)] = `seir/updated_${camelToSnake(prop)}`;
    return dict;
}, {});
export const events = deepFreeze(Object.assign(Object.assign({}, numericEvents), { reset: 'seir/reset' }));
const numericActions = objReduce(reducerDesc, (dict, _, prop) => {
    const cp = camelProp(prop);
    dict[cp] = (input, min) => ({
        type: events[cp],
        payload: reducerDesc[prop].filter(input, min),
    });
    return dict;
}, {});
export const actions = Object.assign(Object.assign({}, numericActions), { reset: () => ({ type: events.reset }) });
const numericreducers = objReduce(defaultSeirState, (dict, _, prop) => {
    const event = events[camelProp(prop)];
    dict[event] = spreadPayloadIfNew(defaultSeirState, prop);
    return dict;
}, {});
export const reducers = Object.assign(Object.assign({}, numericreducers), { [events.reset]: (state = defaultSeirState, _) => {
        noop(state); // preserve arity
        noop(_); // preserve arity
        return defaultSeirState;
    } });
export const createReducerSeirState = (injectable) => createReducer(defaultSeirState, reducers, injectable);
