import { objReduce } from '@ch1/utility';
import { Component, h } from 'preact';
import { connect } from 'react-redux';
import { InputSeir } from '../../components/seir';
import { actions, select } from './seir.state';
class BaseSeirContainer extends Component {
    render() {
        return h(InputSeir, Object.assign({}, this.props));
    }
}
export const SeirContainer = connect(state => objReduce(select, (newStateProps, selector, prop) => {
    newStateProps[prop] = selector(state);
    return newStateProps;
}, actions))(BaseSeirContainer);
