import { objReduce, noop, } from '@ch1/utility';
import { arity } from '../../utility';
export function createReducer(defaultState, reducers, injection) {
    const injectedReducers = injection
        ? objReduce(reducers, (hs, f, key) => {
            if (arity(f) === 1) {
                hs[key] = f(injection);
            }
            else {
                hs[key] = f;
            }
            return hs;
        }, {})
        : reducers;
    return (state = defaultState, action) => {
        const reducer = injectedReducers[action.type];
        if (reducer) {
            return reducer(state, action);
        }
        return state;
    };
}
export function spreadPayloadIfNew(defaultState, prop) {
    return (state = defaultState, { payload }) => {
        if (Array.isArray(payload)) {
            return spreadArrayIfNew(state, payload, prop);
        }
        if (state[prop] === payload) {
            return state;
        }
        return Object.assign(Object.assign({}, state), { [prop]: payload });
    };
}
function spreadArrayIfNew(state, payload, prop) {
    if (payload.length !== state[prop].length) {
        return Object.assign(Object.assign({}, state), { [prop]: payload });
    }
    if (payload.reduce((keepGoing, next, i) => {
        if (keepGoing === false) {
            return keepGoing;
        }
        return state[prop][i] === next;
    }, true)) {
        return state;
    }
    return Object.assign(Object.assign({}, state), { [prop]: payload });
}
export function toggleProp(defaultState, prop) {
    return (state = defaultState, _) => {
        noop(_); // preserve arity
        return Object.assign(Object.assign({}, state), { [prop]: state[prop] ? false : true });
    };
}
