import { Component, h } from 'preact';
import { debounce } from '../../utility';
export class Chart extends Component {
    constructor() {
        super();
        this.componentWillReceiveProps = debounce(props => {
            this.chart = null;
            this.createChart(props);
        });
    }
    createChart(props = this.props) {
        if (!this.chart && props.options.series.length) {
            this.chart = JSC.Chart('chartDiv', props.options);
        }
    }
    componentDidMount() {
        this.base.id = 'chartDiv';
        this.createChart();
    }
    render() {
        return h("div", { className: this.props.flexSize });
    }
}
