import { h } from 'preact';
import { Select } from '../../components/input/select';
import { SelectMultiple } from '../../components/input/select-multiple';
import { InputDate } from '../../components/input/date';
import { Button } from '../../components/input/button';
import { flex, flexCol, flexItem20, highlight, styles } from '../../style';
import { SelectMultipleFilter } from '../../components/input/select-multiple-filter';
export function TimeVsCountsControls({ clearCountries, countries, countryFilter, countryKeys, dataSetIndexes, mode, reload, scaleType, selectedCountry, selectedDataSets, selectedDate, selectedMode, selectedScaleType, showStates, strings, toggledPerCapita, toggledShowSeirState, toggledShowStates, updatedCountryFilter, usePerCapita, ymdStartDate, }) {
    const dataSets = [
        strings.series.activeCases,
        strings.series.confirmedCases,
        strings.series.deaths,
        strings.series.recoveries,
        strings.series.newConfirmed,
        strings.series.newDeaths,
        strings.series.estimatedActiveCases,
        strings.series.projectionSeirActive,
        strings.series.projectionSeirDeaths,
        strings.series.projectionSeirRecoveries,
    ];
    const modes = [
        strings.modes.byDate,
        strings.modes.byFirst,
        strings.modes.byFirst100,
    ];
    const scaleTypes = [
        strings.scaleTypes.linear,
        strings.scaleTypes.logarithmic,
    ];
    const showStatesString = strings.states;
    const metrics = [strings.metrics.byValue, strings.metrics.byPercent];
    return (h("section", { className: `${flex} ${flexItem20}` },
        h("section", { className: flexCol },
            h(Select, { classes: styles.selectBox, onChange: selectedMode, options: modes, selected: mode }),
            h(Select, { classes: styles.selectBox, onChange: selectedScaleType, options: scaleTypes, selected: scaleType }),
            h(InputDate, { classes: styles.input, onChange: selectedDate, ymdString: ymdStartDate }),
            h(Button, { classes: styles.button, label: strings.timeVsCounts.configureSeir, onClick: toggledShowSeirState })),
        h(SelectMultiple, { classes: styles.selectBox, onChange: selectedDataSets, options: dataSets, selected: dataSetIndexes }),
        h(SelectMultipleFilter, { classes: countryKeys.length === 0 ? [highlight] : [], inputButtonClasses: styles.button, inputSelectClasses: styles.selectBox, inputStringClasses: styles.input, filter: countryFilter, onUpdateFilter: updatedCountryFilter, onChange: selectedCountry, onClear: clearCountries, onDeselect: selectedCountry, options: countries, selected: countryKeys }),
        h("div", { className: flexCol },
            h(Select, { classes: styles.selectBox, onChange: toggledShowStates, options: showStatesString, selected: showStates ? 1 : 0 }),
            h(Select, { classes: styles.selectBox, onChange: toggledPerCapita, options: metrics, selected: usePerCapita ? 1 : 0 }),
            h(Button, { classes: styles.button, label: strings.timeVsCounts.reload, onClick: reload }))));
}
